<template>
  <div class="bg-login">
    <div class="d-flex justify-content-center align-items-center mb-4">
      <div class="text-center backgroundImg mt-3"></div>
    </div>
    <div class="d-flex justify-content-center align-items-center">
      <div class="shadow-lg p-3 mb-5 bg-white login">
        <h5><strong>Login</strong></h5>
        <br />
        <div>
          <span class="enter-label text-right">User ID</span
          ><input
            class="border rounded ml-1 enter"
            v-model="username"
            placeholder="your username"
          /><br /><br />
          <span class="enter-label text-right">Password</span
          ><input
            class="border rounded ml-1 enter"
            v-model="password"
            @keypress.enter="login"
            type="password"
          /><br />
          <div class="w-100 text-right pr-4">
            <span class="text-right forget mt-2 d-none">Forget Password</span>
          </div>
          <br />
          <div class="w-100 text-right pr-4">
            <span class="button" @click="login"><strong>Login</strong></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Login } from "../api/auth";
export default {
  data() {
    return {
      username: "",
      password: "",
    };
  },
  watch: {
    username(v) {
      this.username = v.trim();
    },
    password(v) {
      this.password = v.trim();
    },
  },
  methods: {
    login() {
      if (this.username === "" || this.password === "") {
        window.alert("please type username or password!");
        return;
      }

      let api = Login(this.username, this.password);
      api
        .then((res) => {
          if (res.status != 200) {
            window.alert(`系統異常: ${res.statusText}`);
            return;
          }
          return res.json();
        })
        .then((res) => {
          console.log(res);
          if (res.error_code !== 0) {
            window.alert(`帳號密碼錯誤: ${res.error_text}(${res.error_code})`);
            return;
          }

          this.$router.push("/"); // 轉導
        });
    },
  },
  mounted() {},
};
</script>

